import React from 'react';
import { LoggedIn, LoggedOut, AuthButton, Value, List, withWebId } from '@solid/react';
import './App.css';

class App extends React.Component {
  state = { profile: '', viewing: '' };

  componentDidUpdate(prevProps) {
    const { webId } = this.props;
    if (webId && webId !== prevProps.webId)
      this.setState({ profile: webId });
  }

  viewProfile(profile) {
    this.setState({ profile, viewing: profile });
  }

  render() {
    const { profile, viewing } = this.state;
    return (
      <div>
        <h1>Profile viewer</h1>
        <p>
          <LoggedOut>You are not logged in.</LoggedOut>
          <LoggedIn>You are logged in as <Value src="user.name"/>.</LoggedIn>
          <AuthButton popup="popup.html"/>
        </p>
        <p>
          <label htmlFor="profile">Profile:</label>
          <input id="profile" value={profile}
                 onChange={e => this.setState({ profile: e.target.value })}/>
          <button onClick={() => this.viewProfile(profile)}>View</button>
        </p>
        {viewing &&
        <dl>
          <dt>Full name</dt>
          <dd><Value src={`[${viewing}].name`}/></dd>
          <dt>Friends</dt>
          <dd>
            <List src={`[${viewing}].friends`}>{friend =>
              <li key={friend}>
                <button onClick={() => this.viewProfile(friend)}>
                  <Value src={`[${friend}].name`}>{`${friend}`}</Value>
                </button>
              </li>}
            </List>
          </dd>
        </dl>}
      </div>
    );
  }
}
export default withWebId(App);
